	import uCharts from '@/utils/u-charts';
  var canvaLineA=null;
  var _self;
	export default {
    props:['chart'], 
		data() {
			return {
				cWidth:'',
				cHeight:'',
				pixelRatio:1,
			}
		},
		watch: {
			chart () {
				if (canvaLineA&&canvaLineA.updateData) {
					console.log ('折线', this.chart)
					canvaLineA.updateData({
						series: this.chart.series, 
						categories: this.chart.categories,
						yAxis: {
            data:[
              {
                axisLine: false, 
                gridType:'dash',
                min:0,
                max: this.chart.max[0],
								calibration:false,
								position:'left',
                format:(val)=>{return val.toFixed(0)}//如不写此方法，Y轴刻度默认保留两位小数
              }, 
              {
                axisLine: false, 
                min:0 ,
                max: this.chart.max[1] ,
                calibration:false,
                position:'right',
                format:(val)=>{return val.toFixed(0)}
              }
            ], 
            gridType:'dash',
						dashLength:4,
						splitNumber:5
						
					},
					})
					return
				}
				this.showLineA('canvaLineA', this.chart)
			}
		}, 
		created () {
			_self = this;











			this.cWidth=uni.upx2px(630);
      this.cHeight=uni.upx2px(307);
      this.chart.series[1].index=1;
      this.chart.series[0].index=0;
      this.showLineA('canvasLineA',this.chart)
			
		},
		methods: {
			showLineA(canvasId,chartData){
				canvaLineA=new uCharts({
					$this:_self,
					canvasId: canvasId,
					type: 'mix',
					fontSize:uni.upx2px(24),
					dataPointShape:true,
					enableScroll: true,//开启图表拖拽功能
					legend:{
						show:false,
					},
					background:'#FFFFFF',
					pixelRatio:_self.pixelRatio,
					categories: chartData.categories,
					series: chartData.series,
					animation: true,	
					dataLabel:false,
					xAxis: {
						disableGrid:true,
						type:'grid',
						gridType:'dash',
            axisLine: false,
						itemCount:7,
						scrollShow:false,
            scrollAlign:'left', 
					},
					yAxis: {
            data:[
              {
                axisLine: false, 
                gridType:'dash',
                min:0,
                max: chartData.max[0],
								calibration:false,
								position:'left',
                format:(val)=>{return val.toFixed(0)}//如不写此方法，Y轴刻度默认保留两位小数
              }, 
              {
                axisLine: false, 
                min:0 ,
                max: chartData.max[1] ,
                calibration:false,
                position:'right',
                format:(val)=>{return val.toFixed(0)}
              }
            ], 
            gridType:'dash',
						dashLength:4,
						splitNumber:5
						
					},
					width: _self.cWidth*_self.pixelRatio,
					height: _self.cHeight*_self.pixelRatio,
					extra: {
            tooltip:{
							showBox:true,//是否显示半透明黑色的提示区域
							bgColor:'#000000',
							bgOpacity:1,
							gridType:'solid',
							gridColor:'#0586ff',
							fontColor:'#FFFFFF',
							horizentalLine:false,
							xAxisLabel:true,
							yAxisLabel:true,
							labelBgColor:'#DFE8FF',
							labelBgOpacity:0.95,
							labelAlign:'left',
							labelFontColor:'#666666'
						}
					},
				});
				
			},
			touchLineA(e){
				canvaLineA.scrollStart(e);
			},
			moveLineA(e) {
				canvaLineA.scroll(e);
			},
			touchEndLineA(e) {
				canvaLineA.scrollEnd(e);
				//下面是toolTip事件，如果滚动后不需要显示，可不填写
				canvaLineA.touchLegend(e);
				canvaLineA.showToolTip(e, {
					format: function (item, category) {
						return item.name + ':' + item.data 
					}
				});
			},
		}
	}
